import React, { useState } from 'react'
import { Form, Button } from 'react-bootstrap'


const CreateFeedForm = ({ setCreateFeedState, setAlertState, setData }) => {

    const [feedName, setFeedName] = useState('')
    const [feedURL, setFeedURL] = useState('https://docs.google.com/spreadsheets/d/e/2PACX-1vQvm265NJfQjH0GtMLNRD7rJIA5vapYsz1BbIEsmdOYJGuvWlfARCYwy3SS9BmVGGOJ4-MGhcoO7h8w/pub?gid=0&single=true&output=csv')
    

    let handleSubmit = async () => {
        setCreateFeedState("loading")
        setAlertState({ show: false, text: "" })
        console.log("state set to loading");
        
        let res = await fetch(`https://shoevideo.azurewebsites.net/api/check_feed?` + new URLSearchParams({feedURL: feedURL}), {
            method: "GET",
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                
            },
        })

        let data = await res.json();
        if (res.ok) {
            data['feed_name'] = feedName;
            setData(data);
            setCreateFeedState("arrangeFeed");

        }
        else {
            setCreateFeedState("checkURL");
            setAlertState({ show: true, text: data['detail'] })
        }
    }

    return (
        <>
            {/* <Form onSubmit={e => { openModal(); e.preventDefault();}}> */}
            <Form onSubmit={e => {handleSubmit(); e.preventDefault();}}>
                <Form.Group className="mb-3">
                    <Form.Label>Feed Name</Form.Label>
                    <Form.Control required type="text" placeholder="Enter a name for your feed."
                        value={feedName} onInput={e => setFeedName(e.target.value)}  />
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Feed URL</Form.Label>
                    <Form.Control type="text" placeholder="Enter feed URL."
                        value={feedURL} onInput={e => setFeedURL(e.target.value)} />
                </Form.Group>

                <Button variant="primary" type='submit'>Submit</Button>
            </Form>
            
        </>
    )
}

export default CreateFeedForm
